import CryptoJS from 'crypto-js';
// 
const SECRET_KEY = CryptoJS.enc.Utf8.parse("49555f5549");//iu_ui
// 
const SECRET_IV = CryptoJS.enc.Utf8.parse("49555f5549");

//  window.localStorage,window.sessionStorage,
const config = {
    type: 'localStorage', //  sessionStorage
    prefix: 'IU_UI_0.0.1', //   + 
    expire: 3600 * 60 * 24 * 30, // 
    isEncrypt: true //  , 
}

//  Storage
export const isSupportStorage = () => {
    return (typeof (Storage) !== "undefined") ? true : false
}

//  setStorage
export const setStorage = (key, value, expire = 0) => {
    if (value === '' || value === null || value === undefined) {
        value = null;
    }
    if (isNaN(expire) || expire < 0) throw new Error('Expire must be a number');
    expire = (expire ? expire : config.expire) * 1000;
    let data = {
        value: value, // 
        time: Date.now(), //
        expire: expire // 
    }

    const encryptString = config.isEncrypt
        ? encrypt(JSON.stringify(data))
        : JSON.stringify(data)

    window[config.type].setItem(autoAddPrefix(key), encryptString);
}

//  getStorage
export const getStorage = (key) => {
    key = autoAddPrefix(key);
    // key 
    if (!window[config.type].getItem(key) || JSON.stringify(window[config.type].getItem(key)) === 'null') {
        return null;
    }

    //  
    const storage = config.isEncrypt
        ? JSON.parse(decrypt(window[config.type].getItem(key)))
        : JSON.parse(window[config.type].getItem(key));

        console.log(JSON.parse(decrypt(window[config.type].getItem(key))))

    let nowTime = Date.now();

    // 
    if (storage.expire && config.expire * 6000 < (nowTime - storage.time)) {
        removeStorage(key);
        return null;
    } else {
        //   
        setStorage(autoRemovePrefix(key), storage.value);
        return storage.value;
    }
}

//  hasStorage
export const hasStorage = (key) => {
    key = autoAddPrefix(key);
    let arr = getStorageAll().filter((item) => {
        return item.key === key;
    })
    return arr.length ? true : false;
}

// key
export const getStorageKeys = () => {
    let items = getStorageAll()
    let keys = []
    for (let index = 0; index < items.length; index++) {
        keys.push(items[index].key)
    }
    return keys
}

// key
export const getStorageForIndex = (index) => {
    return window[config.type].key(index)
}

// localStorage
export const getStorageLength = () => {
    return window[config.type].length
}


//  getAllStorage
export const getStorageAll = () => {
    let len = window[config.type].length // 
    let arr = new Array() // 
    for (let i = 0; i < len; i++) {
        // key 0
        let getKey = window[config.type].key(i)
        // key
        let getVal = window[config.type].getItem(getKey)
        // 
        arr[i] = { 'key': getKey, 'val': getVal, }
    }
    return arr
}

//  removeStorage
export const removeStorage = (key) => {
    window[config.type].removeItem(autoAddPrefix(key));
}

//  clearStorage
export const clearStorage = () => {
    window[config.type].clear();
}

// 
const autoAddPrefix = (key) => {
    const prefix = config.prefix ? config.prefix + '_' : '';
    return prefix + key;
}

// 
const autoRemovePrefix = (key) => {
    const len = config.prefix ? config.prefix.length + 1 : '';
    return key.substr(len)
    // const prefix = config.prefix ? config.prefix + '_' : '';
    // return  prefix + key;
}

/**
 * 
 * @param data
 * @returns {string}
 */
const encrypt = (data) => {
    if (typeof data === "object") {
        try {
            // console.log(data)
            data = JSON.stringify(data);
            // data = (data + '').replace(/\n*$/g, '').replace(/\n/g, ''); //
        } catch (error) {
            console.log("encrypt error:", error);
        }
    }
    const dataHex = CryptoJS.enc.Utf8.parse(data);
    const encrypted = CryptoJS.AES.encrypt(dataHex, SECRET_KEY, {
        iv: SECRET_IV,
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.Pkcs7
    });
    return encrypted.ciphertext.toString();
}

/**
 * 
 * @param data
 * @returns {string}
 */
const decrypt = (data) => {
    const encryptedHexStr = CryptoJS.enc.Hex.parse(data);
    const str = CryptoJS.enc.Base64.stringify(encryptedHexStr);
    const decrypt = CryptoJS.AES.decrypt(str, SECRET_KEY, {
        iv: SECRET_IV,
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.Pkcs7
    });
    const decryptedStr = decrypt.toString(CryptoJS.enc.Utf8);
    return decryptedStr;
}